@import '../atoms/_button-utils';

$overlay-mask: rgba(get-color('neutral', 7), .73);
$overlay-bg: get-color('neutral', 0);

%overlay {
	height: 0;
	left: 0;
	opacity: 0;
	overflow: hidden;
	position: fixed;
	top: 0;
	transition: background-color animation-frames(25) $transition-ease-out-quart, height 0s linear animation-frames(20), opacity animation-frames(8) $transition-ease-out-quart;
	width: 100%;
	z-index: $z-mid;

	.#{$framework-class-prefix}-overlay-box {
		transform: scale(.95);
		transition: transform animation-frames(13) $transition-ease-out-quart;
	}

	.#{$framework-class-prefix}-close {
		display: none;
	}

	&.#{$framework-class-prefix}-open {
		background-color: $overlay-mask;
		height: 100%;
		opacity: 1;
		transition: background-color animation-frames(13) $transition-ease-out-quart 0ms, height 0ms linear 0ms, opacity 0ms linear 0ms;

		.#{$framework-class-prefix}-overlay-box {
			position: absolute;
			transform: scale(1);
			transition: transform animation-frames(13) $transition-ease-out-quart;
		}

		.#{$framework-class-prefix}-close {
			display: block;
			margin: 1rem;
			padding: 1rem;
			position: absolute !important;
			right: 0;
			top: 0;
			width: auto;
			z-index: $z-mid !important;
		}

	}
}

// .#{$framework-class-prefix}-overlay-container {
// 	// height: 0;
// 	// overflow: hidden;
// }

.#{$framework-class-prefix}-overlay {
	@extend %overlay;
	overflow-y: scroll;

	.#{$framework-class-prefix}-overlay-box {
		margin-bottom: 2em;
		top: 10%;

		.#{$framework-class-prefix}-overlay-content {
			background-color: $overlay-bg;
			margin-bottom: 30px;
		}
	}

	.#{$framework-class-prefix}-overlay-content {
		padding-bottom: 2rem;
		padding-top: 2rem;
	}
}

.#{$framework-class-prefix}-overlay-focus {
	@extend %overlay;

	.#{$framework-class-prefix}-overlay-box {
		background-color: $overlay-bg;
		bottom: 2em;
		overflow-y: scroll;
		position: absolute;
		top: 10%;
	}

	.#{$framework-class-prefix}-overlay-content {
		padding-bottom: 2rem;
		padding-top: 2rem;
	}
}

.#{$framework-class-prefix}-overlay-fullscreen {
	@extend %overlay;
	top: 0;
	z-index: $z-extreme;

	.#{$framework-class-prefix}-overlay-box {
		background-color: $overlay-bg;
		opacity: 0;
		transform: scale(1);
		transition: opacity animation-frames(10) $transition-ease-out-quart;
	}

	&.#{$framework-class-prefix}-open {
		.#{$framework-class-prefix}-overlay-box {
			background-color: $overlay-bg;
			height: 100%;
			opacity: 1;
			overflow-y: scroll;
			top: 0;
			transform: scale(1);
			transition: opacity animation-frames(10) $transition-ease-out-quart;
			width: 100%;
		}
	}

	.#{$framework-class-prefix}-close {
		position: fixed !important;
	}
}
