$codeblock-tab-size: 3;
$codeblock-bg: $code-inline-bg-color;

%codeblock {
	@include text-monospace;
	@include font-weight('medium');
	background-color: $codeblock-bg;
	display: block;
	// padding: 2rem;
	tab-size: $codeblock-tab-size;
}


&.#{$framework-class-prefix}-code {
	@extend %codeblock;
	@include autohide-scrollbarx;

	&.#{$framework-class-prefix}-wrap-text {
		white-space: pre-wrap;
	}
}
