// reset border widths due to weird chrome stuff
@mixin border-reset {
	border-style: solid;
	border-width: 0;
}

%border-reset {
	@include border-reset;
}


//////////////////// Backward Compatibility /////////////////////
@mixin color-class-old($color-class-prefix, $color-properties...) {

	@each $category-name in map-keys($colors-old) {

		$category-list: map-get($colors-old, $category-name);
		$length-of-category-list: length($category-list);

		@for $num from 1 through $length-of-category-list {
			$index: $num;
			$color: nth($category-list, $index);

			.#{$framework-class-prefix}-#{$color-class-prefix}-#{$category-name}-#{$index} {
				@each $property in $color-properties {
					#{$property}: $color !important;
				}

				@content;
			}
		}
	}
}

@include color-class-old('text', 'color');
@include color-class-old('bg', 'background-color');

@include color-class-old('border', 'border-color') {
	@extend %border-reset;
	border-width: 1px;
}

// Commenting out side-related border border styling
// $borders: 'top', 'right', 'bottom', 'left';
// @each $border-side in $borders {
// 	@include color-class-old('border-#{$border-side}', 'border-#{$border-side}-color') {
// 		@extend %border-reset;
// 		border-#{$border-side}-width: 1px;
// 	}
// }

@each $breakpoint in $breakpoints {

	%border-reset-#{$breakpoint} {
		@include border-reset;
	}

	@include breakpoint($breakpoint) {
		@include color-class-old('font-#{$breakpoint}', 'color');
		@include color-class-old('bg-#{$breakpoint}', 'background-color');
	}
}

@each $color-category in map-keys($contextual-colors-old) {
	$color-category-list: map-get($contextual-colors-old, $color-category);

	@for $num from 1 through 5 {

		.#{$framework-class-prefix}-bg-contextual-#{$color-category}-#{$num} {
			background-color: nth($color-category-list, $num);
		}

		.#{$framework-class-prefix}-text-contextual-#{$color-category}-#{$num} {
			color: nth($color-category-list, $num);
		}
	}
}


/////////////////////////////////////////////////////////////////
////////////////////////// New Palette //////////////////////////
/////////////////////////////////////////////////////////////////
@mixin color-class($color-class-prefix, $color-properties...) {

	@each $category-name in map-keys($colors) {

		$category-list: map-get($colors, $category-name);

		@each $color in $category-list {
			$index: index($category-list, $color);

			.#{$framework-class-prefix}-#{$color-class-prefix}-#{$category-name}-#{$index} {
				@each $property in $color-properties {
					#{$property}: $color !important;
				}

				@content;
			}
		}
	}
}

@include color-class('text', 'color');
@include color-class('bg', 'background-color');

@include color-class('border', 'border-color') {
	@extend %border-reset;
	border-width: 1px;
}

