@import '../atoms/utility/_clickable-utils';
@import '../atoms/_button-utils';

$dropdown-basic-option-color: get-color('neutral', 5);
$dropdown-basic-border-color: $button-basic-border-color;
$dropdown-basic-border-width: $button-border-width;
$dropdown-basic-bg: get-color('neutral', 0);
$dropdown-basic-bg-hover: $button-basic-bg-hover;
$dropdown-basic-bg-clicked: $button-basic-bg-clicked;
$dropdown-basic-color-clicked: $button-basic-color-clicked;
$dropdown-border-radius: $button-border-radius;

$dropdown-primary-font-color: $button-primary-font-color;
$dropdown-primary-bg: $button-primary-bg;
$dropdown-primary-bg-hover: $button-primary-bg-hover;
$dropdown-primary-bg-clicked: $button-primary-bg-clicked;

$dropdown-secondary-font-color: $button-secondary-color;
$dropdown-secondary-bg: get-color('neutral', 0);
$dropdown-secondary-bg-hover: $button-secondary-bg-hover;
$dropdown-secondary-border-color: $button-secondary-border;
$dropdown-secondary-color-hover: $button-secondary-color-hover;
$dropdown-secondary-bg-clicked: $button-secondary-bg-clicked;

$dropdown-flat-font-color: $dropdown-basic-option-color;
$dropdown-flat-font-color-hover: $button-font-color;
$dropdown-flat-font-color-clicked: $button-flat-color-clicked;
$dropdown-flat-bg: $dropdown-basic-bg;
$dropdown-flat-bg-hover: $button-flat-bg-hover;
$dropdown-flat-bg-clicked: $dropdown-flat-bg;

%dropdown-core {
	@include margin-bottom;
	background-color: $dropdown-primary-bg;
	border-radius: $dropdown-border-radius;
	color: $dropdown-primary-font-color;
	overflow: hidden;
	position: relative;

	&:focus {
		@include focus-style;
	}

	.#{$framework-class-prefix}-title {
		@include button-core;
		border-radius: $dropdown-border-radius;
		border-style: solid;
		margin-bottom: 0 !important;
		overflow: hidden;
		padding-left: $button-font-size * 1.5;
		padding-right: $button-font-size * 1.5;
		text-overflow: ellipsis;

		&::after {
			bottom: 33%;
			content: '\25BE';
			font-size: 1em;
			height: 1em;
			margin-bottom: 0;
			position: absolute;
			right: .75rem;
		}
	}

	.#{$framework-class-prefix}-options {
		background-color: $dropdown-primary-bg;
		border-bottom-left-radius: $dropdown-border-radius;
		border-bottom-right-radius: $dropdown-border-radius;
		border-style: solid;
		border-width: 0;
		max-height: 0;
		overflow: hidden;
		position: absolute;
		top: 100%;
		transition: max-height $transition-duration * 2, border-width .05s linear $transition-duration * 2 - .1;
		width: 100%;
		z-index: $z-mid;
	}

	.#{$framework-class-prefix}-option {
		@include clickable;
		color: $dropdown-primary-font-color;
		cursor: pointer;
		padding-bottom: .4rem;
		padding-left: $button-font-size * 1.5;
		padding-top: .35rem;

		&:first-child {
			padding-top: .3rem;
		}

		&:hover,
		&:focus {
			background-color: $dropdown-primary-bg-hover;
		}

		&:active {
			background-color: $dropdown-primary-bg-clicked;
		}
	}

	&.#{$framework-class-prefix}-open {
		overflow: visible;

		.#{$framework-class-prefix}-title {
			border-bottom-left-radius: 0;
			border-bottom-right-radius: 0;

			// &::after {
			// 	// transform: translate(0%, -50%) rotateZ(-90deg);
			// }
		}

		.#{$framework-class-prefix}-options {
			max-height: 14rem;
			overflow-y: auto;
			transition: max-height $transition-duration * 2;
		}
	}
}

.#{$framework-class-prefix}-dropdown,
.#{$framework-class-prefix}-dropup {
	@extend %dropdown-core;

	&.#{$framework-class-prefix}-secondary {
		background-color: $dropdown-secondary-bg;
		color: $dropdown-secondary-font-color;

		.#{$framework-class-prefix}-title {
			border-color: $dropdown-secondary-border-color;
			border-width: $button-secondary-border-width;
		}

		.#{$framework-class-prefix}-options {
			background-color: $dropdown-secondary-bg;
			border-color: $dropdown-secondary-border-color;
			border-width: 0 $button-secondary-border-width $button-secondary-border-width;
		}

		.#{$framework-class-prefix}-option {
			color: $dropdown-secondary-font-color;

			&:hover,
			&:focus {
				background-color: $dropdown-secondary-bg-hover;
				color: $dropdown-secondary-color-hover;
			}

			&:active {
				background-color: $dropdown-secondary-bg-clicked;
			}
		}
	}

	&.#{$framework-class-prefix}-flat {
		background-color: $dropdown-flat-bg;
		color: $button-font-color;

		.#{$framework-class-prefix}-title,
		.#{$framework-class-prefix}-options {
			border: 0;
		}

		.#{$framework-class-prefix}-options {
			background-color: $dropdown-flat-bg;
		}


		.#{$framework-class-prefix}-option {
			color: $dropdown-flat-font-color;

			&:hover,
			&:focus {
				background-color: $dropdown-flat-bg-hover;
				color: $dropdown-flat-font-color-hover;
			}

			&:active {
				background-color: $dropdown-flat-bg-clicked;
				color: $dropdown-flat-font-color-clicked;
			}
		}
	}

	&.#{$framework-class-prefix}-basic {
		background-color: $dropdown-basic-bg;
		color: $button-font-color;

		.#{$framework-class-prefix}-title {
			border-color: $dropdown-basic-border-color;
			border-width: $button-border-width;
		}

		.#{$framework-class-prefix}-options {
			background-color: $dropdown-basic-bg;
			border-color: $dropdown-basic-border-color;
			border-width: 0 $button-border-width $button-border-width;
		}

		.#{$framework-class-prefix}-option {
			color: $dropdown-basic-option-color;

			&:hover,
			&:focus {
				background-color: $dropdown-basic-bg-hover;
			}

			&:active {
				background-color: $dropdown-basic-bg-clicked;
				color: $dropdown-basic-color-clicked;
			}
		}
	}

	&.#{$framework-class-prefix}-width-auto {
		display: inline-block;
		vertical-align: top;
		width: auto;

		.#{$framework-class-prefix}-title {
			padding-left: 2rem;
			padding-right: 2rem;
		}
	}
}

.#{$framework-class-prefix}-dropup {

	.#{$framework-class-prefix}-options {
		border-bottom-left-radius: 0;
		border-bottom-right-radius: 0;
		border-top-left-radius: $dropdown-border-radius;
		border-top-right-radius: $dropdown-border-radius;
		bottom: 100%;
		top: auto;
	}

	&.#{$framework-class-prefix}-secondary {
		.#{$framework-class-prefix}-options {
			border-bottom-width: 0;
			border-top-width: $dropdown-basic-border-width;
		}
	}

	&.#{$framework-class-prefix}-basic {
		.#{$framework-class-prefix}-options {
			border-bottom-width: 0;
			border-top-width: $dropdown-basic-border-width;
		}
	}

	&.#{$framework-class-prefix}-open {
		.#{$framework-class-prefix}-title {
			border-bottom-left-radius: $dropdown-border-radius;
			border-bottom-right-radius: $dropdown-border-radius;
			border-top-left-radius: 0;
			border-top-right-radius: 0;
		}
	}
}
