@import '../../_functions';
@import '../../_variables';

$shadow-blue-translucent: rgba(get-color('neutral-cool', 6), .23);

// raised shadow
$shadow-raised-offset-x: 0;
$shadow-raised-offset-y: 1px;
$shadow-raised-blur: 3px;
$shadow-raised: $shadow-raised-offset-x $shadow-raised-offset-y $shadow-raised-blur $shadow-blue-translucent;

// floating shadow
$shadow-floating-offset-x: 0;
$shadow-floating-offset-y: 3px;
$shadow-floating-blur: 6px;
$shadow-floating: $shadow-floating-offset-x $shadow-floating-offset-y $shadow-floating-blur $shadow-blue-translucent;

// text shadow
$shadow-text-offset-x: $shadow-raised-offset-x;
$shadow-text-offset-y: $shadow-raised-offset-y;
$shadow-text-blur: $shadow-raised-blur;
$shadow-text: $shadow-text-offset-x $shadow-text-offset-y $shadow-text-blur $shadow-blue-translucent;


@mixin shadow-raised($important: false) {
	box-shadow: if($important, $shadow-raised !important, $shadow-raised);
}

@mixin shadow-floating($important: false) {
	box-shadow: if($important, $shadow-floating !important, $shadow-floating);
}

@mixin shadow-text($important: false) {
	text-shadow: if($important, $shadow-text !important, $shadow-text);
}
