@import './_accordion-utils';

.#{$framework-class-prefix}-accordion,
.#{$framework-class-prefix}-accordion-small,
.#{$framework-class-prefix}-accordion-large {
	@extend %accordion;

	.#{$framework-class-prefix}-accordion-control {
		@extend %accordion-control;

		&:focus {
			.#{$framework-class-prefix}-accordion-title {
				@include focus-style;
			}
		}
	}

	.#{$framework-class-prefix}-accordion-title {
		@extend %accordion-title;
	}

	.#{$framework-class-prefix}-accordion-slidedown {
		@extend %accordion-slidedown;
	}

	.#{$framework-class-prefix}-open {

		.#{$framework-class-prefix}-accordion-title {
			@extend %accordion-title-open;
		}

		.#{$framework-class-prefix}-accordion-slidedown {
			@extend %accordion-slidedown-open;
		}
	}

	&.#{$framework-class-prefix}-accordion-icon {
		.#{$framework-class-prefix}-accordion-title {
			@extend %accordion-title-icon;
		}

		// right aligned + is optional
		&.#{$framework-class-prefix}-accordion-right {
			.#{$framework-class-prefix}-accordion-title {
				@extend %accordion-right-aligned;
			}
		}
	}

	&.#{$framework-class-prefix}-accordion-right {
		.#{$framework-class-prefix}-accordion-title {
			@extend %accordion-right-aligned;
		}
	}
}

.#{$framework-class-prefix}-accordion-icon {
	@extend %accordion;

	.#{$framework-class-prefix}-accordion-title {
		@extend %accordion-title-icon;
	}
}

.#{$framework-class-prefix}-accordion {
	&.#{$framework-class-prefix}-small {
		.#{$framework-class-prefix}-accordion-title {
			@extend %accordion-title-small;
		}
	}

	&.#{$framework-class-prefix}-large {
		.#{$framework-class-prefix}-accordion-title {
			@extend %accordion-title-large;
		}
	}
}

&.#{$framework-class-prefix}-accordion-small {
	.#{$framework-class-prefix}-accordion-title {
		@extend %accordion-title-small;
	}
}

.#{$framework-class-prefix}-accordion-large {
	.#{$framework-class-prefix}-accordion-title {
		@extend %accordion-title-large;
	}
}
