$loader-neutral-light: get-color('neutral', 1);
$loader-neutral-dark: get-color('neutral', 5);
$loader-blue: get-color('blue', 5);
$loader-blue-background: get-color('neutral-cool', 1);


.#{$framework-class-prefix}-loader-header {
	color: $loader-neutral-dark;
	margin-bottom: 1rem;
	text-align: center;
}

.#{$framework-class-prefix}-loader {
	animation: spin 1s linear infinite;
	border-color: $loader-neutral-light;
	border-radius: 50%;
	border-style: solid;
	border-top-color: $loader-neutral-dark;
	border-top-style: solid;
	border-top-width: 4px;
	border-width: 4px;
	height: (50 / 19) * 1rem;
	margin: 0 auto;
	width: (50 / 19) * 1rem;
}

.#{$framework-class-prefix}-loader-sm {
	height: (26 / 19) * 1rem;
	width: (26 / 19) * 1rem;
}

.#{$framework-class-prefix}-loader-blue {
	.#{$framework-class-prefix}-loader-header {
		color: $loader-blue;
	}

	.#{$framework-class-prefix}-loader {
		border-color: $loader-blue-background;
		border-top-color: $loader-blue;
	}
}

@keyframes spin {
	0% {
		transform: rotate(0deg);
	}

	100% {
		transform: rotate(360deg);
	}
}
