$accordion-border-color: get-color('neutral', 1);
$accordion-closed-bg: get-color('neutral', 0);
$accordion-item-padding-left: 1rem;
$accordion-item-label-padding: 2rem;
$accordion-content-padding-left: 2rem;
$accordion-label: get-color('neutral', 7);
$accordion-active-label-color: get-color('neutral', 5);
$accordion-content-background-color: $accordion-border-color;

%accordion {
	@include no-list-style-type;

	padding-left: 0;

	li {
		border-bottom: 2px solid $accordion-border-color;
	}
}

%accordion-control {
	background-color: $accordion-closed-bg;
	color: $accordion-label;
	cursor: pointer;
	display: block;
	margin-bottom: 0;
	outline: none;
	padding: 0;
	position: relative;
}

%accordion-title {
	padding: .7rem 3rem;
	position: relative;

	&::before {
		@include font-weight('light');
		content: '+';
		// font-family: $font-family-icon;
		font-size: 1.4rem;
		left: $accordion-item-padding-left;
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
	}
}

%accordion-slidedown {
	background-color: $accordion-content-background-color;
	height: 0;
	overflow: hidden;

	& > * {
		opacity: 0;
		transition-delay: $transition-duration / 4;
		transition-duration: $transition-duration;
		transition-property: opacity;
		transition-timing-function: $transition-function;
	}
}

%accordion-title-open {
	color: $accordion-active-label-color;

	&::before {
		transform: rotateZ(45deg) translateY(-50%);
		transform-origin: 50% 0;
	}
}

%accordion-slidedown-open {
	cursor: auto;
	height: auto;
	padding: 2rem;

	& > * {
		opacity: 1;
	}
}

%accordion-right-aligned {
	&::before {
		left: auto;
		right: 1rem;
	}
}

%accordion-title-icon {
	img {
		left: 0;
		max-height: 1.5rem;
		position: absolute;
	}
}

%accordion-title-small {
	padding-bottom: .3rem;
	padding-top: .3rem;
}

%accordion-title-large {
	padding-bottom: 1.2rem;
	padding-top: 1.2rem;
}
