$button-group-primary-border-width: 1px;
$button-group-primary-border-color: rgba(get-color('neutral', 0), .33);

@mixin button-group-core {

	#{$button-selectors} {
		margin-bottom: 0;
		position: relative;

		&:hover,
		&:focus,
		&:active {
			z-index: $z-mid;
		}
	}
}

@mixin collapse-borders-stacked {
	border-radius: 0;
}

@mixin top-border-radius {
	border-top-left-radius: $button-border-radius;
	border-top-right-radius: $button-border-radius;
}

@mixin bottom-border-radius {
	border-bottom-left-radius: $button-border-radius;
	border-bottom-right-radius: $button-border-radius;
}

@mixin vertical-button-border($border-width) {
	border-bottom-width: $border-width;
	border-top-width: $border-width;
	margin-top: -#{px-to-rem($border-width)};

	&:first-child {
		border-top-width: $border-width;
		margin-top: 0;
	}

	&:last-child {
		border-bottom-width: $border-width;
	}
}

@mixin button-group-vertical {
	@include margin-bottom;
	@include button-group-core;

	#{$button-selectors} {
		@include collapse-borders-stacked;
		border-bottom-width: $button-group-primary-border-width;
		border-color: $button-group-primary-border-color;
		border-style: solid;
		border-top-width: $button-group-primary-border-width;

		&:first-child {
			@include top-border-radius;
			border-top-width: 0;
		}

		&:last-child {
			@include bottom-border-radius;
			border-bottom-width: 0;
		}

		&.#{$framework-class-prefix}-secondary {
			@include vertical-button-border($button-secondary-border-width);
		}

		&.#{$framework-class-prefix}-basic {
			@include vertical-button-border($button-border-width);
		}
	}
}

.#{$framework-class-prefix}-button-group {
	@include button-group-vertical;
}

@mixin left-border-radius($px) {
	@if (unitless($px)) {
		$px: #{$px}px;
	}

	border-bottom-left-radius: $button-border-radius;
	border-left-width: #{$px};
	border-top-left-radius: $button-border-radius;
}

@mixin right-border-radius($px) {
	@if (unitless($px)) {
		$px: #{$px}px;
	}

	border-bottom-right-radius: $button-border-radius;
	border-right-width: #{$px};
	border-top-right-radius: $button-border-radius;
}

@mixin horizontal-button {
	display: inline-block;
	font-size: $button-font-size !important;
}

@mixin first-child-horizontal-button {
	@include left-border-radius(0);

	&.#{$framework-class-prefix}-secondary {
		@include left-border-radius($button-secondary-border-width);
	}

	&.#{$framework-class-prefix}-basic {
		@include left-border-radius(2);
	}
}

@mixin last-child-horizontal-button {
	@include right-border-radius(0);

	&.#{$framework-class-prefix}-secondary {
		@include right-border-radius($button-secondary-border-width);
	}

	&.#{$framework-class-prefix}-basic {
		@include right-border-radius(2);
	}
}

@mixin button-group-horizontal {
	@include margin-bottom;
	@include button-group-core;
	font-size: 0 !important;

	#{$button-selectors} {
		@include horizontal-button;
		border-left-width: $button-group-primary-border-width;
		border-radius: 0;
		border-right-width: $button-group-primary-border-width;
		border-style: solid;

		&:first-child {
			@include first-child-horizontal-button;
		}

		&:last-child {
			@include last-child-horizontal-button;
		}

		&.#{$framework-class-prefix}-basic {
			border-right-width: 0;

			&:last-child {
				border-right-width: $button-border-width;
			}
		}

		&.#{$framework-class-prefix}-secondary {
			border-right-width: 0;

			&:last-child {
				border-right-width: $button-secondary-border-width;
			}

			&:hover {
				& + .#{$framework-class-prefix}-button {
					border-left-color: $button-secondary-border-hover;
				}
			}
		}

		&.#{$framework-class-prefix}-flat {
			border-color: $button-group-primary-border-color;
		}
	}
}

.#{$framework-class-prefix}-button-group-h {
	@include button-group-horizontal;
}
