@import '../_mixins';
@import './utility/_clickable-utils';
@import './_typography-utils';

$button-font-size: $font-size-label;
$button-font-size-em: $font-size-label / 1rem * 1em;
$button-font-color: $font-color-default;
$button-background: transparent;
$button-min-padding: .5rem;
$button-line-height: 1.15;
$button-side-padding-modifier: 3.4;
$button-vertical-padding: ((50 / 19) * 1rem) - ($button-line-height * $button-font-size);
$button-vertical-padding-small: ((38 / 19) * 1rem) - ($button-line-height * $button-font-size);
$button-vertical-padding-large: ((75 / 19) * 1rem) - ($button-line-height * $button-font-size);
$button-border-width: 2px;
$button-border-radius: 2px;
$button-small-multiplier: .57;
$button-large-multiplier: 1.65;
$button-hover-transition-properties: background-color, border-color, color;
$button-transition-duration: $transition-duration;
$button-transition-function: $transition-function;

$button-basic-border-color: get-color('neutral', 3);
$button-basic-bg-hover: get-color('neutral', 3);
$button-basic-bg-clicked: $button-background;
$button-basic-color-clicked: get-contextual-color('red', 2);
$button-basic-border-disabled: get-color('neutral', 3);
$button-basic-color-disabled: get-color('neutral', 4);

$button-flat-bg-hover: $button-basic-bg-hover;
$button-flat-bg-clicked: $button-basic-bg-clicked;
$button-flat-color-clicked: $button-basic-color-clicked;
$button-flat-color-disabled: $button-basic-color-disabled;

$button-primary-font-color: $background-color-default;
$button-primary-bg: get-color('blue', 5);
$button-primary-bg-hover: get-contextual-color('blue', 4);
$button-primary-bg-clicked: get-color('blue', 7);
$button-primary-disabled-color: get-color('neutral', 3);
$button-primary-disabled-bg: get-color('neutral', 1);

$button-secondary-bg: $button-background;
$button-secondary-color: $button-primary-bg;
$button-secondary-border: $button-secondary-color;
$button-secondary-border-width: $button-border-width;
$button-secondary-color-hover: get-color('neutral', 0);
$button-secondary-bg-hover: $button-primary-bg-hover;
$button-secondary-border-hover: $button-secondary-bg-hover;
$button-secondary-bg-clicked: $button-primary-bg-clicked;
$button-secondary-color-clicked: $button-secondary-color-hover;
$button-secondary-border-clicked: $button-secondary-bg-clicked;

$button-secondary-disabled-color: get-color('neutral', 3);
$button-secondary-disabled-border: get-color('neutral', 3);

$button-danger-bg: get-contextual-color('red', 3);
$button-danger-font-color: $background-color-default;
$button-danger-bg-hover: get-contextual-color('red', 2);
$button-danger-bg-clicked: get-contextual-color('red', 4);

// icon button
$button-icon-dark: get-color('blue', 7);
$button-icon-dark-hover: get-color('neutral', 6);
$button-icon-dark-clicked: get-contextual-color('red', 2);
$button-icon-neutral: get-color('neutral-warm', 5);
$button-icon-neutral-hover: get-color('neutral-warm', 6);
$button-icon-neutral-clicked: get-color('neutral', 7);
$button-icon-brand: get-color('blue', 5);
$button-icon-brand-hover: get-contextual-color('blue', 4);
$button-icon-brand-clicked: get-color('blue', 7);
$button-icon-inverse: get-color('neutral', 0);
$button-icon-inverse-hover: get-color('neutral-cool', 3);
$button-icon-inverse-clicked: get-color('neutral', 0);
$button-icon-inverse-disabled: get-color('neutral-cool', 5);
$button-icon-disabled: get-color('neutral', 3);

// hover expand
$button-hover-expand-size: .15 * $button-font-size;
$button-hover-expand-transition-properties: bottom, left, right, top;

// inline error colors
$error-red-text: get-contextual-color('red', 3);
$warning-yellow-text: get-color('neutral', 7);
$success-green-text: get-contextual-color('green', 5);

@mixin button-offset-msg {
	padding: 0 .5rem;
}

@mixin button-vertical-padding($size: '') {
	$padding: $button-vertical-padding / 2;
	@if ($size == 'small') {
		$padding: $button-vertical-padding-small / 2;
	}
	@if ($size == 'large') {
		$padding: $button-vertical-padding-large / 2;
	}

	padding-bottom: $padding;
	padding-top: $padding;
}

@mixin button-core {
	@include label;
	@include button-vertical-padding;
	border-radius: $button-border-radius;
	border-style: solid;
	border-width: 0;
	cursor: pointer;
	display: block;
	line-height: $button-line-height;
	position: relative;
	text-align: center;
	transition-duration: $button-transition-duration;
	transition-property: $button-hover-transition-properties;
	transition-timing-function: $button-transition-function;
	z-index: 0;

	&:hover {
		text-decoration: none;
	}

	&:focus {
		@include focus-style;
	}
}

@mixin button-full-width {
	width: 100%;
}

@mixin button-border {
	border: $button-border-width solid $button-basic-border-color;
}

@mixin bordered-button-sizing($line-height, $border-width) {
	$padding: ($button-vertical-padding / 2) - (px-to-rem($border-width));

	padding-bottom: $padding;
	padding-top: $padding;
}

@mixin button-primary {
	background-color: $button-primary-bg;
	border: 0;
	color: $button-primary-font-color;

	&:visited {
		color: $button-primary-font-color;
	}

	&:hover {
		background-color: $button-primary-bg-hover;
		color: $button-primary-font-color;
	}

	&:active {
		background-color: $button-primary-bg-clicked;
		color: $button-primary-font-color;
	}
}

@mixin button-primary-disabled {
	background-color: $button-primary-disabled-bg;
	color: $button-primary-disabled-color;
	cursor: not-allowed;
}

@mixin button-secondary {
	@include bordered-button-sizing('', $button-secondary-border-width);
	background-color: $button-background;
	border: $button-secondary-border-width solid $button-secondary-border;
	color: $button-secondary-color;

	&:visited {
		color: $button-secondary-color;
	}

	&:hover {
		background-color: $button-primary-bg-hover;
		border-color: $button-secondary-border-hover;
		color: $button-primary-font-color;
	}

	&:active {
		background-color: $button-primary-bg-clicked;
		border-color: $button-secondary-border-clicked;
		color: $button-primary-font-color;
	}
}

@mixin button-secondary-disabled {
	background-color: $button-background;
	border-color: $button-secondary-disabled-border;
	color: $button-secondary-disabled-color;
	cursor: not-allowed;
}

@mixin button-flat {
	background-color: $button-background;
	border: 0;
	border-radius: $button-border-radius;
	color: $button-font-color;

	&:visited {
		color: $button-font-color;
	}

	&:hover {
		background-color: $button-flat-bg-hover;
		color: $button-font-color;
	}

	&:active {
		background-color: $button-background;
		color: $button-flat-color-clicked;
	}
}

@mixin button-flat-disabled {
	color: $button-flat-color-disabled;
	cursor: not-allowed;

	&:hover,
	&:active {
		background-color: $button-flat-bg-clicked;
		color: $button-flat-color-disabled;
	}
}

@mixin button-basic {
	@include button-border;
	@include bordered-button-sizing('', $button-border-width);
	background-color: $button-background;
	color: $button-font-color;

	&:visited {
		color: $button-font-color;
	}

	&:hover {
		background-color: $button-basic-bg-hover;
	}

	&:active {
		background-color: $button-basic-bg-clicked;
		color: $button-basic-color-clicked;
	}
}

@mixin button-basic-disabled {
	border-color: $button-basic-border-disabled;
	color: $button-basic-color-disabled;
	cursor: not-allowed;

	&:active,
	&:hover {
		background-color: transparent;
		color: $button-basic-color-disabled;
		text-decoration: none;
	}
}

@mixin button-danger {
	background-color: $button-danger-bg;
	border: 0;
	color: $button-danger-font-color;

	&:visited {
		color: $button-danger-font-color;
	}

	&:hover {
		background-color: $button-danger-bg-hover;
		color: $button-danger-font-color;
	}

	&:active {
		background-color: $button-danger-bg-clicked;
		color: $button-danger-font-color;
	}
}

@mixin button-danger-disabled {
	background-color: $button-primary-disabled-bg;
	color: $button-primary-disabled-color;
	cursor: not-allowed;

	&:active,
	&:hover {
		background-color: $button-primary-disabled-bg;
		color: $button-primary-disabled-color;
		text-decoration: none;
	}
}

@mixin icon-button {
	@include clickable; // utility/_clickable-utils
}

@mixin icon-button-disabled {
	color: $button-icon-disabled;
	cursor: not-allowed;

	&:hover {
		color: $button-icon-disabled;
	}
}

@mixin button-small {
	@include button-vertical-padding('small');
}

@mixin button-large {
	@include button-vertical-padding('large');
}

@mixin button-unresponsive-side-padding($size: '') {
	$padding: 0;

	@if ($size == 'small') {
		$padding: ($button-font-size * $button-small-multiplier * $button-side-padding-modifier);
	} @else if ($size == 'large') {
		$padding: ($button-font-size * $button-large-multiplier * $button-side-padding-modifier);
	} @else {
		$padding: $button-font-size * $button-side-padding-modifier;
	}

	padding-left: $padding;
	padding-right: $padding;
}

@mixin button-unresponsive {
	@include button-unresponsive-side-padding;
	width: auto;
}

@mixin button-unresponsive-small {
	@include button-unresponsive-side-padding('small');
	width: auto;
}

@mixin button-unresponsive-large {
	@include button-unresponsive-side-padding('large');
	width: auto;
}
