@mixin spacing($type, $class-num, $value, $breakpoint: '') {
	.#{$framework-class-prefix}-#{$type}-#{$breakpoint}#{$class-num} {
		#{$type}: $value * $font-size !important;
	}

	@each $side in top, right, bottom, left {
		@include side-spacing($type, $side, $class-num, $value, $breakpoint)
	}
}

@mixin side-spacing($type, $side, $class-num, $value, $breakpoint: '') {

	.#{$framework-class-prefix}-#{$type}-#{$side}-#{$breakpoint}#{$class-num} {
		$property: #{$type}-#{$side};
		#{$property}: $value * $font-size !important;
	}
}

// use above mixins to create margin and padding classes
@each $type in margin, padding {

	@for $var from 0 through 10 {
		@if ($var == 0) {
			@include spacing($type, $var, $var);
		} @else {
			@each $class-num, $value in (#{$var - 1}_5: $var - .5, #{$var}: $var) {
				@include spacing($type, $class-num, $value);
			}
		}
	}
}

@each $breakpoint in $breakpoints {
	@include breakpoint($breakpoint) {
		@each $type in margin, padding {
			@for $var from 0 through 10 {
				@if ($var == 0) {
					@include spacing($type, $var, $var, #{$breakpoint}-);
				} @else {
					@each $class-num, $value in (#{$var - 1}_5: $var - .5, #{$var}: $var) {
						@include spacing($type, $class-num, $value, #{$breakpoint}-);
					}
				}
			}
		}
	}
}
