@font-face {
	font-family: "w3ds-icons";
	src: url('./icons/w3ds-icons.woff2') format('woff2'),
		url('./icons/w3ds-icons.woff') format('woff'),
		url('./icons/w3ds-icons.ttf') format('truetype');
}

@mixin ds-icon-styles {
	font-family: "w3ds-icons";
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
	font-style: normal;
	font-variant: normal;
	font-weight: normal !important;
	letter-spacing: 0;
	line-height: .9;
	// speak: none; // only necessary if not using the private unicode range (firstGlyph option)
	text-decoration: none;
	text-transform: none;
}

%ds-icon {
	@include ds-icon-styles;
}

