@import '_button-utils';

%icon-button {
	@include icon-button;
}

%icon-button-disabled {
	@include icon-button-disabled;
}

#{$button-selectors} {
	@include button-core;
	@include button-primary;

	&:not([class*='#{$framework-class-prefix}-col']) {
		@include button-full-width;
	}

	&.#{$framework-class-prefix}-small {
		@include button-small;
	}

	&.#{$framework-class-prefix}-large {
		@include button-large;
	}

	&.#{$framework-class-prefix}-disabled {
		@include button-primary-disabled;
	}

	&.#{$framework-class-prefix}-secondary {
		@include button-secondary;

		&.#{$framework-class-prefix}-disabled {
			@include button-secondary-disabled;
		}

		&.#{$framework-class-prefix}-small {
			@include bordered-button-sizing('small', $button-secondary-border-width);
		}

		&.#{$framework-class-prefix}-large {
			@include bordered-button-sizing('large', $button-secondary-border-width);
		}
	}

	&.#{$framework-class-prefix}-flat {
		@include button-flat;

		&.#{$framework-class-prefix}-disabled {
			@include button-flat-disabled;
		}

		&.#{$framework-class-prefix}-current {
			position: relative;

			&::before {
				background-color: $black;
				bottom: 0;
				content: '';
				height: .2rem;
				left: 0;
				position: absolute;
				width: 100%;
			}
		}
	}

	&.#{$framework-class-prefix}-basic {
		@include button-basic;

		&.#{$framework-class-prefix}-disabled {
			@include button-basic-disabled;
		}

		&.#{$framework-class-prefix}-small {
			@include bordered-button-sizing('small', $button-border-width);
		}

		&.#{$framework-class-prefix}-large {
			@include bordered-button-sizing('large', $button-border-width);
		}

		&.#{$framework-class-prefix}-current {
			position: relative;
			z-index: 101;

			&::before {
				content: '';
				height: 100%;
				left: 0;
				outline: 2px solid $black;
				position: absolute;
				top: 0;
				width: 100%;
			}
		}
	}

	&.#{$framework-class-prefix}-danger {
		@include button-danger;

		&.#{$framework-class-prefix}-disabled {
			@include button-danger-disabled;
		}
	}

	&.#{$framework-class-prefix}-full-border {

		&.#{$framework-class-prefix}-secondary {
			border-width: $button-secondary-border-width !important;
		}

		&.#{$framework-class-prefix}-basic {
			border-width: $button-border-width !important;
		}
	}

	&.#{$framework-class-prefix}-width-auto {
		@include button-unresponsive;

		&.#{$framework-class-prefix}-small {
			@include button-unresponsive-small;
		}

		&.#{$framework-class-prefix}-large {
			@include button-unresponsive-large;
		}
	}

	&.#{$framework-class-prefix}-truncate {
		@include truncate;
	}

	& + .#{$framework-class-prefix}-button-msg {
		margin-top: -$margin-bottom;
	}
}

.#{$framework-class-prefix}-button-msg {
	@include button-offset-msg;
	text-align: center;

	&.#{$framework-class-prefix}-error {
		color: $error-red-text;
	}

	&.#{$framework-class-prefix}-warning {
		color: $warning-yellow-text;
	}

	&.#{$framework-class-prefix}-success {
		color: $success-green-text;
	}
}

&.#{$framework-class-prefix}-icon-button-dark {
	@extend %icon-button;
	color: $button-icon-dark;

	&:hover {
		color: $button-icon-dark-hover;
	}

	&:active {
		color: $button-icon-dark-clicked;
	}

	&.#{$framework-class-prefix}-disabled {
		@extend %icon-button-disabled;
	}
}

&.#{$framework-class-prefix}-icon-button-neutral {
	@extend %icon-button;
	color: $button-icon-neutral;

	&:hover {
		color: $button-icon-neutral-hover;
	}

	&:active {
		color: $button-icon-neutral-clicked;
	}

	&.#{$framework-class-prefix}-disabled {
		@extend %icon-button-disabled;
	}
}

&.#{$framework-class-prefix}-icon-button-brand {
	@extend %icon-button;
	color: $button-icon-brand;

	&:hover {
		color: $button-icon-brand-hover;
	}

	&:active {
		color: $button-icon-brand-clicked;
	}

	&.#{$framework-class-prefix}-disabled {
		@extend %icon-button-disabled;
	}
}

&.#{$framework-class-prefix}-icon-button-inverse {
	@extend %icon-button;
	color: $button-icon-inverse;

	&:hover {
		color: $button-icon-inverse-hover;
	}

	&:active {
		color: $button-icon-inverse-clicked;
	}

	&.#{$framework-class-prefix}-disabled {
		color: $button-icon-inverse-disabled;
		cursor: not-allowed;
	}
}

