@import './carousel-utils.scss';

.#{$framework-class-prefix}-carousel {
	@extend %carousel;
}

.#{$framework-class-prefix}-carousel-items {
	@extend %carousel-items;
}

.#{$framework-class-prefix}-carousel-arrow {
	@extend %carousel-move-triggers;

	.#{$framework-class-prefix}-carousel-img-bg {
		@extend %carousel-move-triggers-img-bg;
	}

	.#{$framework-class-prefix}-carousel-dark & {
		@extend %carousel-move-triggers-light;
	}

	&.#{$framework-class-prefix}-left {
		@extend %carousel-move-trigger-left;
	}

	&.#{$framework-class-prefix}-right {
		@extend %carousel-move-trigger-right
	}

	&.#{$framework-class-prefix}-disabled {
		@extend %carousel-move-triggers-disabled;
	}
}

.#{$framework-class-prefix}-carousel-position-indicator {
	@extend %carousel-position-indicator-container;
}

.#{$framework-class-prefix}-indicator {

	.#{$framework-class-prefix}-circle & {
		@extend %carousel-position-indicator-circle;

		&.#{$framework-class-prefix}-selected {
			@extend %carousel-position-indicator-circle-selected;
		}
	}

	.#{$framework-class-prefix}-color & {
		@extend %carousel-position-indicator-circle-blue;

		&.#{$framework-class-prefix}-selected {
			@extend %carousel-position-indicator-circle-blue-selected;
		}
	}

	.#{$framework-class-prefix}-number & {
		@extend %carousel-position-indicator-number;

		&.#{$framework-class-prefix}-selected {
			@extend %carousel-position-indicator-number-selected;
		}
	}

	.#{$framework-class-prefix}-condensed & {
		@extend %carousel-position-indicator-condensed;
	}
}

.#{$framework-class-prefix}-condensed {

	.#{$framework-class-prefix}-carousel-arrow {
		@extend %carousel-move-triggers-condensed;
	}
}
